.divider:after,
.divider:before {
  content: "";
  flex: 1;
  height: 1px;
  background: #eee;
}
.h-custom {
  height: calc(100% - 73px);
}
@media (max-width: 450px) {
  .h-custom {
    height: 100%;
  }
}

.border {
  border-color: white !important;
}
.login {
  background-image: url("../../../public/images/login.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  margin-top: -22px;
  margin-bottom: -4% !important;
  height: 110vh !important;
}
.login a {
  cursor: pointer;
}

.blur {
  backdrop-filter: blur(5px);
  color: white;
}

.white {
  color: white !important;
}
.center-input .border {
  backdrop-filter: blur(5px);
  margin-top: 100px !important;
}
.center-input input {
  width: 20vw;
  margin-top: 2vh;
  padding: 10px;
}
.center-input button {
  background-color: #44e2a8;
  border: none;
  width: 100%;
}
.center-input button:hover {
  background-color: #7149f2;
}
.background {
  background-image: url("https://i.pinimg.com/originals/b1/f7/58/b1f7587c85c3b7b581d72bc9de751423.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 90vh;
  margin-top: -8vh;
}
