.home-page h1 {
  font-weight: 600;
  background-color: #d27ef9;
  color: white;
  border-radius: 25px;
}

.swiper {
  width: 100%;
  height: 600px;
}
.swiper img {
  width: 100%;
  height: 150%;
}

.home-page {
  display: flex;
  flex-wrap: wrap;
}
.home-page .card {
  width: 20rem;
  height: 38rem;
}

.home-page .card-name-price {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.home-page .card-name-price svg {
  height: 20px;
  margin-right: 5px;
  font-weight: 700;
}
.home-page .card-name-price .color {
  background-color: #ff9f00;
}
.home-page .card-name-price .color-1 {
  background-color: #fb641b;
}

.home-page .card-price {
  color: rgb(123, 215, 81);
  font-weight: bold;
}
.home-page button {
  border-radius: 0;
  width: 100%;
  border-radius: 20px;
  font-size: 14px;
  font-weight: bold;
}
.home-page button:hover {
  box-shadow: inset 0 -3.25em 0 0 rgb(104, 204, 234);
  border: none;
}

.home-page .card-text {
  color: rgb(90, 89, 89);
}
.home-page .overflow {
  overflow: hidden;
  min-height: 45vh;
}

.home-page .card img:hover {
  transform: scale(1.2);
  transition: all 0.4s ease;
}

.home-page .card-img-top {
  height: 350px !important;
  border-radius: 0%;
}
/* ==================
filters
============== */

.border {
  border: 2px solid rgb(180, 178, 178) !important;
  padding-bottom: 20px;
  border-radius: 25px;
  font-weight: 500;
  position: sticky;
}
.filters h4 {
  margin-top: 100px;
  border-bottom: 1px solid rgba(128, 128, 128, 0.337);
}
.text {
  margin-bottom: -10vh;
  color: white;
  background-color: rgb(87, 210, 103);
  padding: 5px 0;
  margin: -2px -2px -10vh -2px;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  border-bottom: 1px solid rgba(128, 128, 128, 0.337);
}
.ant-checkbox-wrapper:first-of-type {
  margin-left: 5px;
}

.ant-checkbox-wrapper {
  margin-left: 20px !important;
}
.ant-radio-wrapper {
  margin-bottom: 5px !important;
  margin-left: 20px;
}

.filters button {
  background-color: rgb(228, 103, 242);
  width: 97% !important;
  margin: 0 15px;
  padding: 9px;
  border: none;
  border-radius: 12px;
  margin-top: 20px;
}
.filters button:hover {
  box-shadow: inset 0 -3.25em 0 0 rgb(91, 206, 241);
  border: none;
}

.loadmore {
  color: rgb(72, 237, 72);
  font-weight: bold;
  font-size: 20px !important;
}
.loadmore:hover {
  color: white;
}
